import { publishCourse } from 'app/js/course-manage/help';
import 'store';


export const courseFunctionRemask = () => {
  const COURSE_FEATURE_REMIND = 'COURSE-FEATURE-REMIND'; //课程改版功能提醒
  if (!store.get(COURSE_FEATURE_REMIND)) {
    store.set(COURSE_FEATURE_REMIND, true);
    $('#course-function-modal').modal('show');
  }
}


publishCourse();


startIntro();
function startIntro(){

  console.log($('#step-1'));
  let intro = introJs();
  intro.setOptions({
    steps: [
      { 
        intro: "课程管理功能现已全新升级!",
      },
      { 
        element: '#step-1',
        intro: "教学内容的编辑、管理请点击左侧“计划任务”的菜单项进入!",
      },
      { 
        element: '#step-2',
        intro: "在“营销设置”中您可以通过设置决定课程如何销售、如何加入、如何学习!",
      }
    ],
    skipLabel:'我知道了',
    nextLabel:'下一步',
    prevLabel:'上一步'
  });
  console.log(intro);
  intro.start();
}
    
